<div class="container pers">
  {#if post}
    <div in:fade>
      <h2>
        {@html marked(post.title)}
      </h2>
      <h5 class="">
        {new Date(post.timestamp).toLocaleDateString(undefined, {
          year: 'numeric',
          month: 'long',
          day: 'numeric',
        })}
      </h5>
      <span class="padding">{@html marked(post.body)}</span>
